import smooth from '@/helpers/smooth.js'

export default {
    data() {
        return { currentComponent: '', timer: null, min: '' }
    },
    inject: ['eventBus'],
    mounted() {
        this.$route.path.match(/^\/\w+\/\w+\/(\w+)$/)
        let name = RegExp.$1
        if (name) {
            //这里异步有点小问题
            setTimeout(() => {
                this.smoothScroll(name)
            }, 0);
        }
        window.addEventListener('scroll', this.listenScroll)
        this.eventBus.$on('link-component', this.smoothScroll)
    },
    methods: {
        smoothScroll(name) {
            if (this.$refs[name]) {
                let y = this.$refs[name].offsetTop - 88;
                y !== window.scrollY && smooth(window, { x: 0, y });
            }
        },
        listenScroll() {
            this.timer ? window.clearTimeout(this.timer) : '';
            this.timer = setTimeout(() => {
                let scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
                let scrollHeight = document.body.scrollHeight;
                let clientHeight = document.documentElement.clientHeight;
                let refs = this.$refs;
                let keys = Object.keys(refs);
                this.min = this.min || keys[0];
                keys.forEach(key => {
                    let top = Math.abs(refs[key].getBoundingClientRect().top);
                    top < Math.abs(this.$refs[this.min].getBoundingClientRect().top) && top < clientHeight - 100 ? this.min = key : '';
                });
                if (scrollHeight - scrollTop <= clientHeight + 88) {
                    this.min = keys[keys.length - 1];
                }
                if (this.currentComponent !== this.min) {
                    this.currentComponent = this.min;
                    this.$route.path.match(/^\/(\w+\/\w+\/)\w+$/)
                    let prefix = RegExp.$1
                    this.eventBus.$emit('instage-component', prefix + this.currentComponent);
                }
            }, 400);
        }
    },
    beforeDestroy() {
        this.eventBus.$off('click-tab', this.smoothScroll);
        window.removeEventListener('scroll', this.listenScroll);
        this.timer ? window.clearTimeout(this.timer) : '';
        this.timer = null;
    }
}